  class SendEmail extends Component {
   /*
        These are the step values we're interested in
        props.steps.email.value
        props.steps.name.value
        props.steps.message.value
        props.steps.contactpermission.value
    */

    constructor(props) {
      super(props);
      
      // console.log(props)
  
      this.state = {
        loading: true,
        result: 0,
        trigger: false,
        apiKey: props.apiKey,
        PrivacyPolicyAgree: true,
        // AudienceAgree: props.steps.contactpermission.value,
        SenderFirstName: props.steps.name.value,
        // SenderLastName: "",
        SenderEmail: props.steps.email.value,
        // SenderPhone: messagePhone,
        SenderSubject: "Chat Bot Message",
        SenderData: props.steps.message.value
      };
  
      this.triggerNext = this.triggerNext.bind(this);
    }

    sendEmail(url) {

    // Create a data object for the state
    const FormDataObject = Object({
        PrivacyPolicyAgree: this.state.PrivacyPolicyAgree,
        // AudienceAgree: this.state.AudienceAgree,
        SenderFirstName: this.state.SenderFirstName,
        SenderEmail: this.state.SenderEmail,
        SenderSubject: "Chat Bot Message",
        SenderData: this.state.SenderData
        // SenderPhone: messagePhone,
        // SenderLastName: "",
    });

      this.setState({...this.state, loading: true});  

      let MPformData = new FormData()
    
      MPformData.append('FormData', JSON.stringify(FormDataObject))

      let fullUrl = url + "/" + this.state.apiKey
      
      axios({
        method: "post",
        url: fullUrl,
        data: MPformData,
        headers: { "Content-Type": "multipart/form-data" },
      })
      .then(response => {
          // console.log(response)
          if (response.status == 200) {
            this.setState({result: "Message sent. Someone will be in touch!", loading: false})  
          } else {
            this.setState({result: "There may have been an issue. Please try an alternate method of contact.", loading: false})  
          }
      })
      .catch(e => {
          console.log(e);
          this.setState({...this.state, loading: false});
      });

  };
  
    componentDidMount() {
      const self = this;
      const { steps } = this.props;
      // const search = steps.search.value;
  
      const queryUrl = `https://api.contactform.is/contactnp`;
        
      this.sendEmail(queryUrl);
      
    }
  
    triggerNext() {
      this.setState({ trigger: true }, () => {
        this.props.triggerNextStep();
      });
    }
  
    render() {
      const { trigger, loading, result } = this.state;
  
      return (
        <div className="sendemail">
          { loading ? <Loading /> : result }
        </div>
      );
    }
  }

  class CheckEmail extends Component {
    /*
         These are the step values we're interested in
         props.steps.email.value
     */
 
     constructor(props) {
       super(props);
       
       // console.log(props)
   
       this.state = {
         result: "...",
         loading: true,
         trigger: false,
         SenderEmail: props.steps.email.value,
         apiKey: props.apiKey,
         emailValid: ''
       };
   
       this.triggerNext = this.triggerNext.bind(this);
     }
 
     checkEmail() {

      const fullUrl = "https://api.contactform.is/validateemail/" + this.state.apiKey + "/" + this.state.SenderEmail
      // console.log("Full URL = " + fullUrl)
      const encodedUrl = encodeURI(fullUrl);

      axios.get(encodedUrl)
      .then(response => {
        if (response.status == 200) {
          let responseCode = response.data.ResponseCode.toLowerCase()

          if (responseCode == "valid" || responseCode == "valid_catchall") {
 
            let fullUrl = "https://api.contactform.is/verifyemail/" + this.state.apiKey + "/" + this.state.SenderEmail
            
            axios({
              method: "get",
              url: fullUrl,
            })
            .then(response => {
              // console.log(response)
                
                if (response.status == 200) {
                  if (response.data == "verified") {
                    // GO TO STEP X
                    // this.setState({...this.state, loading: false, result: "Your email is already verified."});
                    this.setState({...this.state, loading: false, result: "How can we help you?"});
                    let config = { trigger: 'message' }
                    this.triggerNext(config)
                  }
                  if (response.data == "unverified") {
                    // GO TO STEP X
                    this.setState({...this.state, loading: false, result: "Check your email for a validation email and please follow the instructions."});
                    let config = { trigger: '6' }
                    this.triggerNext(config)
                  }
                } else {
                  // GO TO STEP Y
                  this.setState({...this.state, loading: false, result: "Check your email for a validation email and please follow the instructions."});
                  let config = { trigger: '6' }
                  this.triggerNext(config)
                }
            })
            .catch(e => {
                console.log(e);
                this.setState({...this.state, loading: false});
            });
          } else if (responseCode == "invalid") {
            this.setState({...this.state, loading: false, result: "Your email is not valid."});
          } else {
            this.setState({...this.state, loading: false, result: "Your email doesn't appear to be valid."});
          }
        }
      })
      .catch(e => {
          console.log(e);
          return 
      })
     };
   
     componentDidMount() {
       this.checkEmail();
     }
   
     triggerNext(nextStep) {
       this.setState({ trigger: true }, () => {
         this.props.triggerNextStep(nextStep);
       });
     }
   
     render() {
       const { trigger, loading, result } = this.state;
   
       return (
         <div className="sendemail">
           { result }
         </div>
       );
     }
   }

  class VerifyEmail extends Component {
    // WIP
    /*
      1.  Send verification email to customer
      2.  Wait for 3 mins in 3 second loops to see if email has been verfied
          https://api.contactform.is/emailverified/:id/:email
      3.  if 200, get JSON and check for {verified: BOOL} data structure
      4.  Else timeout
    */
    constructor(props) {
      super(props);
  
      this.state = {
        loading: true,
        result: '',
        trigger: false,
        apiKey: props.apiKey,
        email: props.steps.email.value,
        resendstatus: 0,
      };
  
      this.triggerNext = this.triggerNext.bind(this);
    }

    reSendVerificationEmail() {

      const self = this;
      const url = `https://api.contactform.is/resendverificationemail`;

      const fullUrl = url + "/" + this.state.apiKey + "/" + this.state.email
      // console.log("Full URL = " + fullUrl)
      const encodedUrl = encodeURI(fullUrl);

      axios.get(encodedUrl)
      .then(response => {
        // console.log(response)
        if (response.status == 200) {

          this.setState({...this.state, resendstatus: response.status, result: 'Resent Verification Email', loading: false});
          let config = { trigger: 'resend' }
          this.triggerNext(config)
        }
      })
      .catch(e => {
          console.log(e);
          this.setState({...this.state, loading: false});
      });
    }

    fetchVerifyEmail(url) {

      const self = this;

      const fullUrl = url + "/" + this.state.apiKey + "/" + this.state.email
      // console.log("Full URL = " + fullUrl)
      const encodedUrl = encodeURI(fullUrl);
      
      this.setState({...this.state, loading: true});

      // This timer governs the master timeout (3 mins for prod)
      this.outerTimeout = setTimeout(() => {
        this.setState({...this.state, loading: false, result: "timeout"});
        clearTimeout(this.innertimer1);
        clearTimeout(this.innertimer2);
        clearTimeout(this.outerTimeout);
        clearInterval(threeSecondCheck)
      }, mailVerificationTimeout);

      // This is the first pass
      axios.get(encodedUrl)
      .then(response => {
        // console.log(response)
        if (response.data !== '') {
          this.setState({...this.state, result: response.data});

          // If we get a response, then move on in the chat workflow
          if (this.state.result == 'verified') { 
            this.innertimer1 = setTimeout(() => {
              this.setState({...this.state, loading: false, });
              clearTimeout(this.innertimer1);
              clearTimeout(this.innertimer2);
              clearTimeout(this.outerTimeout);
              clearInterval(threeSecondCheck)
              this.triggerNext()
            }, 1000);
          }
        }
      })
      .catch(e => {
          console.log(e);
          this.setState({...this.state, loading: false});
      });
      // End of first pass


      // Three second interval 
      let threeSecondCheck = setInterval(function() {
        axios.get(encodedUrl)
        .then(response => {
          // console.log(response)
          if (response.data !== '') {
            this.setState({...this.state, result: response.data});

            // If we get a response, then move on in the chat workflow
            if (this.state.result == 'verified') {
              this.setState({...this.state, loading: false});
              clearTimeout(this.innertimer1);
              clearTimeout(this.innertimer2);
              clearTimeout(this.outerTimeout);
              clearInterval(threeSecondCheck)
              this.innertimer2 = setTimeout(() => {
                this.triggerNext()
              }, 1000);
            }

          }
        })
        .catch(e => {
            console.log(e);
            this.setState({...this.state, loading: false});
        });
      }.bind(this), 3000);
      
        
      // TIMER LOOP END

    };
  
    componentDidMount() {
      const self = this;
  
      const queryUrl = `https://api.contactform.is/verifyemail`;
  
    
      this.fetchVerifyEmail(queryUrl);

    }

    componentWillUnmount() {
      clearTimeout(this.innertimer1);
      clearTimeout(this.innertimer2);
      clearInterval(threeSecondCheck);
    }
  
  
    triggerNext(nextStep) {
      this.setState({ trigger: true }, () => {
        this.props.triggerNextStep(nextStep);
      });
    }
  
    render() {
      const { trigger, loading, result } = this.state;

      let displayResult = null

      if (result == "verified") {
        displayResult = 
        <>
          <div style={{ textAlign: 'center', fontSize: '12px'}}>
            <div>Email Verified</div>
            <div>Thank You</div>
          </div>
        </>
      } else if (result == "timeout") {
        displayResult = 
        <>
          <div style={{ textAlign: 'center', fontSize: '12px'}}>
            <div>Email Not Verified</div>
            <div>Please Try Again</div>
            <br/>
            <button
                onClick={() => this.reSendVerificationEmail()}
              >
                Resend Verification Email
              </button>
          </div>
        </>
      } else {
        displayResult = 
        <>
          <div style={{ textAlign: 'center', fontSize: '12px'}}>
            <div>{this.state.result}</div>
          </div>
        </>
      }
  
      return (
        <div className="verifyemail">
          { loading ? <Loading /> : displayResult }
        </div>
      );
    }
  }

